






































import { Component, Vue } from "vue-property-decorator";

@Component
export default class SettingDrawer extends Vue {
  visible = false;

  onClose() {
    this.visible = false;
  }

  OnThemeChange(type: string, v: string) {
    this.$router.push({ query: { ...this.$route.query, [type]: v } });
  }
}
